___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: rgb(14, 94, 137);
    --primary-color-dark: rgb(9, 64, 94);
    --second-color: rgb(241, 245, 247); 
}

* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
    font-size: 1.6rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: var(--primary-color);
}

h1 {
    font-size: 3rem;
    margin-bottom: 2rem;
}

h2 {
    font-size: 2rem;
}

.container {
    background-color: var(--second-color);
    box-sizing: border-box;
    max-width: 70rem;
    margin: 10vh auto;
    padding: 3rem;
    font-size: 2rem;
}

.senha-gerada {
    margin-bottom: 2.5rem;
    color: var(--primary-color);
    font-size: 3rem;
}

#caracteres {
    width: 6rem;
}

.filtros div{
    display: block;
    margin-bottom: 0.5rem;
}

button {
    margin-top: 2rem;
    font-size: 2rem;
    box-sizing: border-box;
    padding: 0rem 0.5rem 0rem 0.5rem;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,iCAAiC;IACjC,oCAAoC;IACpC,kCAAkC;AACtC;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,sHAAsH;IACtH,sCAAsC;AAC1C;;AAEA;IACI,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,qCAAqC;IACrC,sBAAsB;IACtB,gBAAgB;IAChB,iBAAiB;IACjB,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,2BAA2B;IAC3B,eAAe;AACnB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,sBAAsB;IACtB,gCAAgC;AACpC","sourcesContent":[":root {\r\n    --primary-color: rgb(14, 94, 137);\r\n    --primary-color-dark: rgb(9, 64, 94);\r\n    --second-color: rgb(241, 245, 247); \r\n}\r\n\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nhtml {\r\n    font-size: 62.5%;\r\n}\r\n\r\nbody {\r\n    font-size: 1.6rem;\r\n    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;\r\n    background-color: var(--primary-color);\r\n}\r\n\r\nh1 {\r\n    font-size: 3rem;\r\n    margin-bottom: 2rem;\r\n}\r\n\r\nh2 {\r\n    font-size: 2rem;\r\n}\r\n\r\n.container {\r\n    background-color: var(--second-color);\r\n    box-sizing: border-box;\r\n    max-width: 70rem;\r\n    margin: 10vh auto;\r\n    padding: 3rem;\r\n    font-size: 2rem;\r\n}\r\n\r\n.senha-gerada {\r\n    margin-bottom: 2.5rem;\r\n    color: var(--primary-color);\r\n    font-size: 3rem;\r\n}\r\n\r\n#caracteres {\r\n    width: 6rem;\r\n}\r\n\r\n.filtros div{\r\n    display: block;\r\n    margin-bottom: 0.5rem;\r\n}\r\n\r\nbutton {\r\n    margin-top: 2rem;\r\n    font-size: 2rem;\r\n    box-sizing: border-box;\r\n    padding: 0rem 0.5rem 0rem 0.5rem;\r\n}"],"sourceRoot":""}]);
